/* MaterialDesignIcons.com */
@font-face {
  font-family: "Material Design Icons";
  src: url("mdi-icons/fonts/materialdesignicons-webfont.eot?v=5.5.55");
  src: url("mdi-icons/fonts/materialdesignicons-webfont.eot?#iefix&v=5.5.55")
      format("embedded-opentype"),
    url("mdi-icons/fonts/materialdesignicons-webfont.woff2?v=5.5.55")
      format("woff2"),
    url("mdi-icons/fonts/materialdesignicons-webfont.woff?v=5.5.55")
      format("woff"),
    url("mdi-icons/fonts/materialdesignicons-webfont.ttf?v=5.5.55")
      format("truetype");
  font-weight: normal;
  font-style: normal;
}

.mdi:before,
.mdi-icon.set,
.mdi-icon {
  display: inline-block;
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.iconpicker-component .mdi-icon,
.iconpicker-item .mdi-icon {
  font-size: 40px;
  line-height: 40px;
}

.icon-select .mdi-icon,
.widget .mdi-icon {
  font-size: 30px;
  line-height: 40px;
  display: inline-block;
  width: 40px;
}

.mdi-icon {
  width: 85px;
  height: 85px;
  background-color: transparent;
  color: #aaa;
}

.titleIcon.titel {
  margin-left: 55px;
  height: 55px;
}
.titleIcon.mdi-icon {
  width: 55px;
  height: 55px;
  background-color: transparent;
  color: var(--text-color);
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 35px;
  line-height: 35px;
}

.indicatoricon.mdi-icon {
  width: 44px;
  height: 44px;
  background-color: transparent;
  color: #aaa;
  font-size: 40px;
  line-height: 30px;
  margin: auto;
}

.compactMode.mdi-icon {
  width: 40px;
  height: 40px;
  font-size: 30px;
  line-height: 40px;
}
.compactMode.titleIcon.titel {
  margin-left: 40px;
  height: 40px;
}
.compactMode.titleIcon.mdi-icon {
  width: 40px;
  height: 40px;
  font-size: 30px;
  line-height: 30px;
}

.compactMode.sliderIcon.mdi-icon {
  width: 30px;
  height: 30px;
  font-size: 30px;
  line-height: 30px;
}

.hell .titleIcon.mdi-icon {
  color: #1f1f21;
}

.pageIconHolder {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.pageIcon.mdi-icon {
  width: 30px;
  height: 30px;
  background-color: transparent;
  color: var(--text-color);
  display: inline-block;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
}

.hell .pageIcon.mdi-icon {
  color: #1f1f21;
}

.sliderIcon.mdi-icon {
  width: 50px;
  height: 50px;
  background-color: transparent;
  color: var(--text-color);
  display: inline-block;
  font-size: 35px;
  line-height: 50px;
}

.hell .sliderIcon.mdi-icon {
  color: #1f1f21;
}

.valueSwitcherIcon.mdi-icon {
  background-color: transparent;
  display: block;
  width: 35px;
  height: 40px;
  margin: 0 auto;
  font-size: 35px;
  line-height: 40px;
}

.button .valueSwitcherIcon.mdi-icon {
  color: var(--button-bar-active-text-color);
}

.button--outline .valueSwitcherIcon.mdi-icon {
  color: var(--text-color);
}

.hell .valueSwitcherIcon.mdi-icon {
  background-color: transparent;
  color: #1f1f21;
}

.mdi-icon.on,
.mdi-icon.true {
  background-color: transparent;
  color: #66cc33;
}

.mdi-icon.alarm {
  background-color: transparent;
  color: #66cc33;
}

.mdi-icon.alarm.true,
.mdi-icon.alarm.error,
.mdi-icon.alarm.fault,
.mdi-icon.alarm.fehler {
  background-color: transparent;
  color: #fe3824;
}

