.em:before,
.em-icon.set,
.em-icon {
  display: inline-block;
  font-size: inherit;
  text-rendering: auto;
  line-height: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.iconpicker-component .em-icon,
.iconpicker-item .em-icon {
  font-size: 40px;
  line-height: 40px;
}

.icon-select .em-icon,
.widget .em-icon {
  font-size: 30px;
  line-height: 40px;
  display: inline-block;
  width: 40px;
}

.em-icon {
  width: 85px;
  height: 85px;
  background-color: transparent;
  color: #aaa;
}

.titleIcon.titel {
  margin-left: 55px;
  height: 55px;
}
.titleIcon.em-icon {
  width: 55px;
  height: 55px;
  background-color: transparent;
  color: var(--text-color);
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 35px;
  line-height: 35px;
}

.indicatoricon.em-icon {
  width: 40px;
  height: 40px;
  background-color: transparent;
  color: #aaa;
  font-size: 35px;
  line-height: 30px;
  margin: 0;
}

.compactMode.em-icon {
  width: 40px;
  height: 40px;
  font-size: 30px;
  line-height: 40px;
}
.compactMode.titleIcon.titel {
  margin-left: 40px;
  height: 40px;
}
.compactMode.titleIcon.em-icon {
  width: 40px;
  height: 40px;
  font-size: 30px;
  line-height: 30px;
}

.compactMode.sliderIcon.em-icon {
  width: 30px;
  height: 30px;
  font-size: 30px;
  line-height: 30px;
}

.hell .titleIcon.em-icon {
  color: #1f1f21;
}

.pageIconHolder {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.pageIcon.em-icon {
  width: 30px;
  height: 30px;
  background-color: transparent;
  color: var(--text-color);
  display: inline-block;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
}

.hell .pageIcon.em-icon {
  color: #1f1f21;
}

.sliderIcon.em-icon {
  width: 50px;
  height: 50px;
  background-color: transparent;
  color: var(--text-color);
  display: inline-block;
  font-size: 35px;
  line-height: 50px;
}

.hell .sliderIcon.em-icon {
  color: #1f1f21;
}

.valueSwitcherIcon.em-icon {
  background-color: transparent;
  display: block;
  width: 40px;
  height: 40px;
  margin: 0 auto;
  font-size: 30px;
  line-height: 40px;
}

.button .valueSwitcherIcon.em-icon {
  color: var(--button-bar-active-text-color);
}

.button--outline .valueSwitcherIcon.em-icon {
  color: var(--text-color);
}

.hell .valueSwitcherIcon.em-icon {
  background-color: transparent;
  color: #1f1f21;
}

.em-icon.on,
.em-icon.true {
  background-color: transparent;
  color: #66cc33;
}

.em-icon.alarm {
  background-color: transparent;
  color: #66cc33;
}

